.catalog {
  max-width: 100%;
  box-sizing: border-box;
  margin: auto;
  font-family: Epilogue;
}

.wrap {
  max-width: 1400px;
}

.catalog-title {
  color: #1e2827;
  letter-spacing: -.02em;
  text-align: left;
  margin-bottom: 20px;
  padding-top: 80px;
  padding-left: 50px;
  font-size: 40px;
  font-weight: 600;
  line-height: 44px;
  display: flex;
}

.main-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 24px;
  display: flex;
}

.list-element {
  list-style: none;
  position: relative;
}

.overlay-info {
  min-height: auto;
  opacity: 0;
  visibility: hidden;
  width: 90%;
  background-color: #1e2827;
  border-radius: 8px;
  padding: 20px;
  transition: opacity 1s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: auto;
}

.list-element:hover .overlay-info {
  visibility: visible;
  opacity: 1;
  transition: opacity 1s;
}

.overlay-info h3 {
  letter-spacing: -.02em;
  text-align: left;
  color: #fff;
  font-family: Epilogue;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.overlay-info p {
  letter-spacing: -.01em;
  text-align: left;
  color: rgba(255, 255, 255, .5);
  font-family: Epilogue;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.watch-image {
  max-width: 100%;
  height: auto;
  margin-bottom: 14px;
  display: block;
}

.info-name {
  max-width: 276px;
  color: #141414;
  letter-spacing: -.02em;
  text-align: left;
  max-height: 18px;
  font-family: Epilogue;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  overflow-y: hidden;
}

.info-price {
  letter-spacing: -.02em;
  text-align: left;
  color: rgba(20, 20, 20, .5);
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.buy-button {
  color: #fff;
  text-align: center;
  background-color: #1e2827;
  border-radius: 8px;
  margin-bottom: 80px;
  margin-left: auto;
  padding: 18px 35px;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 11;
}

[data-modal] {
  height: 100%;
  width: 100%;
  z-index: 11;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden [data-modal-content] {
  transform: translate(-50%, -50%)scale(.5);
}

[data-modal-content] {
  max-width: 904px;
  max-height: 694px;
  z-index: 11;
  background-color: #fff;
  border-radius: 4px;
  padding: 20px;
  transition-duration: 1s;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 2px 1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
}

[data-modal-close] {
  cursor: pointer;
  text-align: center;
  width: 30px;
  height: 30px;
  color: #000;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  flex-shrink: 0;
  font-size: 18px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 8px;
  right: 8px;
}

[data-modal-close]:hover, [data-modal-close]:focus {
  color: var(--blue);
}

.modal-watch {
  align-items: center;
}

.modal-form {
  flex-direction: column;
  gap: 12px;
  padding: 12px 0;
  display: flex;
}

.modal-form__position {
  letter-spacing: .01em;
  color: #757575;
  max-width: 508px;
  flex-direction: column;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
  position: relative;
}

.modal-form__position input {
  border-radius: 8px;
  padding-left: 10px;
}

.modal-form__position textarea {
  border-radius: 8px;
}

.modal-form__input {
  height: 35px;
  cursor: pointer;
  border: 1px solid rgba(33, 33, 33, .2);
  outline: none;
  margin-top: 4px;
  padding-left: 12px;
}

.modal-form__comments {
  height: 35px;
  cursor: pointer;
  height: 120px;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  margin-top: 4px;
  padding-top: 5px;
  padding-left: 12px;
}

.modal-form__input:hover, .modal-form__input:focus, .modal-form__comments:hover, .modal-form__comments:focus {
  border-color: #00f;
}

.condition {
  color: var(--blue);
}

.modal-form__input[type="checkbox"] {
  display: none;
}

.modal-form__input[type="checkbox"]:checked + .replace-checkbox {
  background-color: var(--blue);
  border-color: var(--blue);
}

.modal-form__input[type="checkbox"]:checked + .modal-checkbox__position svg {
  display: block;
}

.modal-form__btn {
  width: auto;
  height: 50px;
  cursor: pointer;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #1e2827;
  border: none;
  border-radius: 8px;
  outline: none;
  margin: 25px 0 0;
  font-family: Epilogue;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.modal-form__btn:hover {
  box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
}

.submit-info {
  letter-spacing: -.02em;
  text-align: left;
  color: rgba(20, 20, 20, .4);
  font-family: Epilogue;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.policy {
  text-decoration: underline;
}

@media screen and (max-width: 768px) {
  .list-element:nth-last-child(-n+4) {
    display: none;
  }

  [data-modal-content] {
    max-height: 1073px;
    flex-direction: column;
    display: flex;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
  .list-element:nth-last-child(-n+4) {
    display: none;
  }

  [data-modal-content] {
    min-width: 744px;
  }
}

@media screen and (min-width: 1280px) {
  .modal-form__btn {
    min-width: 508px;
  }
}

